/* oswald-regular - latin_cyrillic */
@font-face {
     font-family: 'Oswald';
     font-style: normal;
     font-weight: 400;
     font-display: swap;
     src: local('Oswald-Regular'),
          local('Oswald Regular'),
          url('../fonts/oswald-v36-latin_cyrillic-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
          url('../fonts/oswald-v36-latin_cyrillic-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
   }
/* oswald-500 - latin_cyrillic */
@font-face {
     font-family: 'Oswald';
     font-style: normal;
     font-weight: 500;
     font-display: swap;
     src: local('Oswald Medium'),
          local('Oswald-Medium'),
          url('../fonts/oswald-v36-latin_cyrillic-500.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
          url('../fonts/oswald-v36-latin_cyrillic-500.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
   }